import { createSlice } from "@reduxjs/toolkit";
import { getTemplate } from "./data";

const templatorSlice = createSlice({
  name: "templator",
  initialState: {
    name: "Ravi",
    company: "Amazon",
    experience: 2,
    skills: `javascript, coding, web development`,
    template: ``
    //getTemplate({ name, company, experience, skills })
  },

  reducers: {
    updateItems: (state, action) => {
      switch (action.payload.fieldName) {
        case "name":
          state[action.payload.fieldName] = action.payload.fieldValue;
          break;
        case "experience":
          state[action.payload.fieldName] = action.payload.fieldValue;
          break;
        case "skills":
          state[action.payload.fieldName] = action.payload.fieldValue;
          break;
        case "company":
          state[action.payload.fieldName] = action.payload.fieldValue;
          break;
        default:
          console.error("unexpected");
      }
    },

    createTemplate: (state) => {
      state.template = getTemplate({
        name: state.name,
        company: state.company,
        experience: state.experience,
        skills: state.skills
      });
    },

    updateTemplate: (state, action) => {
      // console.log(action);
      state.template = action.payload;
    }
  }
});

export const {
  createTemplate,
  updateItems,
  updateTemplate
} = templatorSlice.actions;
export default templatorSlice.reducer;
