import {
  Autocomplete,
  Box,
  Button,
  Card,
  FormControl,
  FormHelperText,
  TextField,
  InputLabel,
  TextareaAutosize
} from "@mui/material";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { copy } from "../data";
import { updateTemplate } from "../templatorSlice";

export default function TemplateItem() {
  const [disabled, setDisabled] = useState(true);
  const dispatch = useDispatch();
  const template = useSelector((state) => state.templator.template);

  return (
    <Box
      style={{
        margin: "10px auto 10px 10px",
        width: "90%",
        maxWidth: "600px",
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        border: "2px solid #ccc",
        borderRadius: "8px"
      }}
    >
      <TextareaAutosize
        style={{
          width: "99%",
          maxWidth: "600px",
          padding: "12px 20px",
          boxSizing: "border-box",
          border: "none",
          backgroundColor: "transparent",
          outline: "none",
          fontSize: "16px",
          resize: "none",
          height: "300px",
          lineHeight: 1.75,
          // textAlign: "justify",
          overflow: "auto"
        }}
        readOnly={disabled}
        aria-label="maximum height"
        placeholder="Click on create button"
        value={template}
        onChange={(e) => dispatch(updateTemplate(e.target.value))}
      />
      <Box
        style={{
          width: "100%",
          borderTop: "solid lightBlue 1px",
          display: "flex"
        }}
      >
        <Button
          style={{
            flexGrow: "1"
          }}
          onClick={() => copy(template)}
        >
          Copy
        </Button>
        <Button
          style={{
            height: "40px",
            borderLeft: "solid lightBlue 1px",
            zIndex: 10,
            borderRadius: "0px"
          }}
          onClick={() => setDisabled(false)}
        >
          edit
        </Button>
      </Box>
    </Box>
  );
}
