export const getTemplate = ({ name, company, experience, skills }) => {
  console.log({ skills });
  const skillsSimp = skills?.split(",").join(", ");

  const temp = `hi ${name}, 
      I am looking out for a job change. I have ${experience} years of experience in ${skillsSimp}, can you please refer me to ${company} for this job proifile attaching my resume for reference.`;
  console.log(temp);
  return temp;
};

